@charset 'UTF-8';

//------------------------------------
//  FORM
//------------------------------------

body {
	background-color: #f1f1f1;

	&,
	.box {
		height: 100vh;
	}
}

.form {
	background-color: #fff;
	box-shadow: 0 1px 2px #aaa;
	padding: 35px 20px;
	width: 100%;

	h1 {
		@include font-responsive(28);
		margin: 0;
		margin-bottom: 15px;
	}

	.form__input-mask {
		position: relative;
		border: 1px solid #ccc;
		margin: 10px 0;
		overflow: hidden;

		&:before {
			@include font-responsive(18);
			@include contentBefore;
			background-color: #ccc;
			content: 'R$';
			height: 50px;
			line-height: 50px;
			margin-right: 4%;
			top: 0;
			width: 50px;
		}

		&.form__input-mask--horas {
			&:before {
				@include font-responsive(22);
				content: '\231A';
			}
		}
	}

	input,
	button {
		@include font-responsive(15);
		border: none;
		height: 50px;
		width: 100%;
	}

	input {
		background-color: #f1f1f1;
		padding: 5px 20px 5px 70px;
	}

	// .form__calcular { }
}